import { Component, OnInit, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, Validators, FormGroup } from '@angular/forms';
import RegExp from '../../config/regex';
import { ApiService } from 'src/app/services/api.service';
import { ModalControllerService } from 'src/app/components/modal/services/modal-controller.service';
import { AlertComponent } from 'src/app/components/alert/alert.component';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  public title: string = 'Contact';
  public contactForm: FormGroup;

  constructor(
    private form: FormBuilder, 
    private apiService: ApiService,
    private modal: ModalControllerService) {
    this.createForm();
  }

  ngOnInit() { }

  createForm() {
    this.contactForm = this.form.group({
      firstName: new FormControl(null, [ 
        Validators.required,
        Validators.maxLength(50)
      ]),
      lastName: new FormControl(null, [ 
        Validators.required,
        Validators.maxLength(50)
      ]),
      email: new FormControl(null, [ 
        Validators.required,
        Validators.maxLength(50),
        Validators.pattern(RegExp.email) 
      ]),
      contactNo: new FormControl(null, [ 
        Validators.maxLength(25),
        Validators.required 
      ]),
      message: new FormControl(null, [ 
        Validators.maxLength(750),
        Validators.required 
      ])
    });
  }

  send() {
    this.contactForm.markAllAsTouched();
    this.contactForm.markAsDirty();
    this.contactForm.updateValueAndValidity();
    
    if(this.contactForm.valid) {
      this.apiService.contactSend(this.contactForm.value)
        .then(_ => this.contactForm.reset())
        .then(_ => this.sent());
    }
  }

  sent() {

    var okay = new EventEmitter();
    okay.subscribe(_ => this.modal.close('contact-message-sent-alert'));

    this.modal.create({
      id: 'contact-message-sent-alert',
      header: 'I got your message',
      component: AlertComponent,
      props: {
        content: 'Thank you for sending me a message.',
        subContent: 'It is nice to have opportunity doing business with you.',
        okay: okay
      },
      animate: true
    });

    this.modal.open('contact-message-sent-alert');

  }

  getValue(formControlName: string) {
    return this.contactForm.controls[formControlName].value;
  }

  hasInvalidTouchedDirty(formControlName: string, error: string) {
    return this.contactForm.controls[formControlName].invalid 
    && (this.contactForm.controls[formControlName].dirty 
        || this.contactForm.controls[formControlName].touched)
    && this.contactForm.controls[formControlName].hasError(error)
  }

  goto(url: string) {
    window.open(url, '_blank');
  }

}
