export default "/* Media Queries in Mobile First Approach */\n/* Media Queries in Web First Approach */\n.contact {\n  display: grid;\n  grid-template-columns: 1fr;\n  justify-items: center;\n}\n.contact .information {\n  padding-top: 3rem;\n  max-width: 400px;\n}\n.contact .information button {\n  display: flex;\n  align-items: center;\n  margin: 1rem 1rem;\n}\n.contact .information button svg {\n  margin-right: 1rem;\n  width: 2rem;\n  border-radius: 0.25rem;\n}\n.contact .information button:hover {\n  color: var(--accent);\n}\n.contact .information button:hover svg {\n  box-shadow: var(--button-icon-black-shadow);\n}\n.contact .form {\n  max-width: 500px;\n  min-width: 20rem;\n  padding-top: 3rem;\n  display: grid;\n  position: relative;\n  /* .input-group {\n      display: grid;\n  } */\n}\n.contact .form .not-available {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  display: flex;\n  background-color: var(--black-50);\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n}\n.contact .form .not-available h2,\n.contact .form .not-available p {\n  color: var(--white);\n  text-align: center;\n}\n.contact .form .input-group * {\n  width: 100%;\n  display: block;\n}\n.contact .form .input-group .error {\n  color: var(--danger);\n}\n.contact .form label,\n.contact .form .input {\n  margin-bottom: 0.2rem;\n}\n.contact .form .input:last-child {\n  margin-bottom: 0.5rem;\n}\n.contact .form .error {\n  margin-bottom: 0.5rem;\n}\n.contact .form button {\n  margin-top: 0.5rem;\n}\n.contact .form .form-group {\n  display: grid;\n  -moz-column-gap: 0.5rem;\n       column-gap: 0.5rem;\n  grid-template-columns: 1fr;\n}\n@media (min-width : 768px) {\n  .contact {\n    grid-template-columns: 1fr 1fr;\n  }\n  .contact .form .form-group {\n    grid-template-columns: 1fr 1fr;\n  }\n}\n/* @media (min-width : 500px) {\n    .contact {\n        .information {\n            grid-template-columns: 1fr 1fr;\n        }\n    }\n} */\n/*# sourceMappingURL=data:application/json;base64, */"